@import "../reset.scss";

.module {
  // ==================================================================
  @include smallDevice {
    width: 92vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    overflow: hidden;
    text-decoration: none;

    .image {
      height: 250px;
      width: 100%;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        height: 100%;
        width: auto;
        transition: transform 0.5s ease;
        transform-origin: center center;
        justify-self: center;
        align-self: center;

        &:hover {
          transform: scale(1.05);
        }
      }
    }

    .information {
      width: 100%;
      display: flex;
      justify-content: space-between;
      font-weight: bold;
      padding: 10px;
      box-sizing: border-box;
      font-size: 25px;

      .title {
        color: black;
      }

      .price {
        display: flex;
        justify-content: flex-end;
        gap: 3px;
        .price_old {
          text-decoration: line-through;
          color: lightgray;
          font-size: 20px;
          font-weight: 400;
        }
        .price_new {
          color: rgb(187, 28, 28);
        }
      }
    }

    .availability {
      width: 100%;
      display: flex;
      justify-content: flex-start;
      padding: 0px 10px;
      margin-top: -5px;
      box-sizing: border-box;
      h1 {
        font-style: italic;
        font-size: 15px;
        font-weight: 200;
        color: rgb(90, 90, 90);
        &.avail_active {
          color: rgb(167, 46, 46);
          font-weight: 500;
        }
      }
    }
  }

  // ==================================================================
  @include mediumDevice {
    // width: 1fr;
    max-width: 320px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    overflow: hidden;
    text-decoration: none;

    .status {
      display: flex;
      width: 100%;
      justify-content: flex-start;
      margin-bottom: 3px;
      font-weight: 200;
      font-size: 10px;

      .none {
        border-radius: 20px;
        background-color: #f2de79;
        padding: 5px 10px;
        display: flex;
        align-items: center;
        gap: 5px;
        h1 {
          &:first-child {
            color: black;
          }
        }
        div {
          color: #f2cb05;
          background-color: #f2f2f2;
          border-radius: 50%;
          width: 13px;
          height: 13px;
          display: flex;
          justify-content: center;
          align-items: center;
          font-weight: 800;
          font-size: 12px;
        }
      }

      .reserved {
        border-radius: 20px;
        padding: 5px 10px;
        color: white;
      }
    }
    .image {
      height: 250px;
      width: 100%;
      overflow: hidden;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        height: 100%;
        width: auto;
        transition: transform 0.5s ease;
        transform-origin: center center;
        justify-self: center;
        align-self: center;

        &:hover {
          transform: scale(1.05);
        }
      }
    }

    .information {
      width: 100%;
      display: flex;
      justify-content: space-between;
      font-weight: bold;
      padding: 10px;
      box-sizing: border-box;
      font-size: 25px;

      .title {
        color: black;
      }

      .price {
        display: flex;
        justify-content: flex-end;
        gap: 5px;
        .price_old {
          text-decoration: line-through;
          color: lightgray;
          font-size: 20px;
        }
        .price_new {
          color: rgb(187, 28, 28);
        }
      }
    }

    .availability {
      width: 100%;
      display: flex;
      justify-content: flex-start;
      padding: 0px 10px;
      margin-top: -5px;
      box-sizing: border-box;
      h1 {
        font-style: italic;
        font-size: 17px;
        font-weight: 200;
        color: rgb(90, 90, 90);
        &.avail_active {
          color: rgb(167, 46, 46);
          font-weight: 500;
        }
      }
    }
  }
}
