@import "../reset.scss";

.page {
  @include smallDevice {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    .header {
      height: auto;
      width: 100vw;
      display: flex;
      justify-content: center;
      padding-top: 30px;
      .slide {
        height: 60vw;
        width: 110vw;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        div {
          height: 100%;
          width: 100%;
          margin: 0;
          img {
            width: 100%;
            height: auto;
          }
        }
      }
    }

    .description {
      width: 90%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 30px;
      .des_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .header_title {
          display: flex;
          flex-direction: column;
          gap: 3px;
          justify-content: flex-start;
          margin: 0;
          .title_h1 {
            text-transform: capitalize;
            font-size: 30px;
            height: fit-content;
            margin: 0;
            padding-top: 10px;
          }
          .title_h2 {
            font-size: 14px;
            margin: 0;
            color: gray;
            font-weight: 400;
          }
        }
        .header_price {
          display: flex;
          justify-content: flex-end;
          gap: 3px;
          .price_old {
            color: rgb(147, 147, 147);
            text-decoration: line-through;
            font-size: 12px;
          }
          .price_new {
            color: rgb(199, 22, 22);
            font-size: 30px;
          }
        }
      }

      .des_body {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding: 0px;
        gap: 10px;
        box-sizing: border-box;
        height: auto;
        .info {
          display: flex;
          justify-content: space-between;
          border-bottom: solid 1px lightgray;
          color: gray;
          padding: 20px;
          box-sizing: border-box;

          .info_left {
            font-size: 17px;
            font-weight: 400;
          }
          .info_this {
            font-size: 16px;
            font-weight: 300;
          }
        }
      }

      .info_note {
        padding: 20px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        color: gray;
        gap: 15px;

        .invisible {
          display: none;
        }
        .note {
          font-size: 15px;
          font-weight: 300;
          &::before {
            content: "✓ ";
          }
        }
      }
    }
  }

  // ===============================================================
  @include mediumDevice {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .header {
      height: 45vw;
      width: 100vw;
      margin: -30px;
      background-color: rgb(0, 0, 0);
      .slide {
        height: 100%;
        width: 92%;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        div.board {
          height: 90%;
          margin: 0;
          margin-top: -5vw;
        }
      }
    }

    .description {
      width: 80%;
      display: flex;
      flex-direction: column;
      padding-top: 70px;
      .des_header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .header_title {
          display: flex;
          flex-direction: column;
          gap: 3px;
          justify-content: flex-start;
          margin: 0;
          .title_h1 {
            text-transform: capitalize;
            font-size: 60px;
            height: fit-content;
            margin: 0;
            padding-top: 10px;
          }
          .title_h2 {
            font-size: 30px;
            margin: 0;
            color: gray;
            font-weight: 400;
          }
        }
        .header_price {
          display: flex;
          justify-content: flex-end;
          gap: 10px;
          .price_old {
            color: rgb(147, 147, 147);
            text-decoration: line-through;
            font-size: 45px;
            font-weight: 400;
          }
          .price_new {
            color: rgb(199, 22, 22);
            font-size: 65px;
          }
        }
      }
      .des_body {
        padding: 50px 20px 10px 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        .info {
          display: flex;
          justify-content: space-between;
          border-bottom: solid 1px lightgray;
          color: gray;
          padding: 20px;
          box-sizing: border-box;

          .info_left {
            font-size: 30px;
            font-weight: 400;
          }
          .info_this {
            font-size: 28px;
            font-weight: 300;
          }
        }

        .info_note {
          padding: 20px;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          color: gray;
          gap: 15px;

          .invisible {
            display: none;
          }
          .note {
            font-size: 26px;
            font-weight: 300;
            &::before {
              content: "✓ ";
            }
          }
        }
      }

      .des_form {
        align-self: center;
        width: fit-content;
        display: flex;
        flex-direction: column;
        gap: 30px;
        margin: 30px 10px 50px 10px;
        padding: 50px 80px;
        font-size: 20px;
        color: gray;
        font-weight: 300;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
        .reservation {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          // align-items: center;
          gap: 20px;
          .block {
            display: flex;
            flex-direction: column;
            gap: 5px;
            input,
            select {
              border: solid 1px #dddddd;
              border-radius: 5px;
              width: 220px;
              height: fit-content;
              padding: 5px 0px;
              &:focus {
                box-shadow: none;
                border: 1px solid #ebc460;
              }
              &::placeholder {
                font-size: 17px;
              }
            }
            select {
              background-color: rgba(255, 255, 255, 0.422);
              font-size: 16px;
              color: gray;
              font-weight: 300;
            }
            .required {
              &::after {
                content: "*";
                color: red;
              }
            }
          }

          .button {
            width: 220px;
            padding: 10px 20px;
            text-align: center;
            text-transform: capitalize;
            transition: 0.5s;
            background-size: 200% auto;
            color: white;
            border-radius: 10px;
            display: block;
            border: 0px;
            font-size: 17px;
            font-weight: 500;
            box-shadow: 0px 0px 14px -7px #f09819;
            background-image: linear-gradient(
              45deg,
              #ff512f 0%,
              #f09819 51%,
              #ff512f 100%
            );
            cursor: pointer;
            user-select: none;
            -webkit-user-select: none;
            touch-action: manipulation;
          }

          .button:hover {
            background-position: right center;
            color: #fff;
            text-decoration: none;
          }

          .button:active {
            transform: scale(0.95);
          }
        }
        .rule {
          display: flex;
          flex-direction: column;
          gap: 10px;
          p {
            color: gray;
            text-indent: -25px;
            margin-left: 25px;
            // text-align: justify;
            &::before {
              content: "✓ ";
            }
          }
        }
        .waitlist {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          gap: 0px;
          .wait_header {
            display: flex;
            align-items: center;
            gap: 8px;
            h1 {
              font-size: 20px;
              color: gray;
              font-weight: 300;
              padding: 20px 0px;
            }
            div {
              height: 15px;
              width: 15px;
              font-size: 10px;
              background-color: slategray;
              color: white;
              font-weight: 400;
              display: flex;
              justify-content: center;
              align-items: center;
              border-radius: 50%;
              position: relative;
            }
          }
          .wait_turn {
            padding: 10px 20px;
            background-color: transparent;
            border: none;
            margin: 0;
            text-align: inherit;
            font: inherit;
            border-radius: 0;
            appearance: none; // Just in case we missed anything.
            cursor: pointer;

            &:nth-child(2n) {
              background-color: #e5e5e553;
            }
            h1 {
              font-size: 18px;
              font-weight: 100;
              text-transform: capitalize;
              text-wrap: nowrap;
            }
            &:hover {
              box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
            }

            display: grid;
            grid-template-columns: 30px 2fr 3fr 3fr;
          }

          .canceled {
            text-decoration: line-through;
            text-decoration-color: rgb(219, 0, 0);
            h1 {
              text-decoration-thickness: 0.3px;
              color: rgb(219, 0, 0);
            }
          }
        }
      }
    }
  }
}
