@import "../reset.scss";

.app {
  box-sizing: border-box;

  // ===================================================================
  @include smallDevice() {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 30px;

    .header {
      height: 150px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 30px 30px;

      h1 {
        margin: 0px;
        font-weight: 900;
        line-height: 70px;
        font-size: 80px;
        text-transform: uppercase;
        text-align: center;
      }
    }

    .keynote {
      h1 {
        font-size: 30px;
        font-weight: 500;
        color: rgb(84, 84, 84);
        padding-top: 20px;
        padding-bottom: 20px;
      }
      p {
        color: gray;
        font-size: 20px;
        text-indent: -20px;
        padding-left: 20px;
        line-height: 30px;
      }
    }

    .shopping {
      padding: 20px 20px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 20px;
    }
  }

  // ===================================================================
  @include mediumDevice() {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0px 30px 30px 30px;

    .header {
      display: flex;
      justify-content: center;
      align-items: center;
      height: fit-content;
      width: 100vw;
      padding: 150px 0px;
      color: white;
      // background: url("https://t3.ftcdn.net/jpg/03/02/04/06/360_F_302040655_IEH9RyDlu7LL8YCLjgL1IskhrpOlmlSv.jpg");
      background-image: url("https://st4.depositphotos.com/1005515/28446/i/450/depositphotos_284465010-stock-photo-simple-blurred-abstract-background.jpg");
      background-size: cover;
      h1 {
        margin: 0px;
        font-weight: 900;
        font-size: 80px;
        text-transform: uppercase;
        text-align: center;
      }
    }

    .keynote {
      padding: 30px 10px;
      h1 {
        font-size: 30px;
        font-weight: 500;
        color: navy;
        padding-top: 20px;
        padding-bottom: 20px;
      }
      p {
        color: slategray;
        font-size: 20px;
        text-indent: -20px;
        padding-left: 20px;
        line-height: 30px;
      }
    }

    .shopping {
      padding: 20px 20px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 20px;
    }
  }
}
